import React, { useContext } from 'react'
import { css } from '@emotion/core'
import { StorageContext } from './StorageContext'
import { H3 } from '../base-components/Headers'

const drawerCss = css`
  transition: margin-right 0.2s ease-in-out;
  display: flex;
  flex-direction: column;
  width: 385px;
  min-width: 385px;
  color: var(--color-text);
  box-sizing: border-box;
  overflow-y: scroll;
`

const titleCss = css`
  padding: 0 16px;
  height: 40px;
  display: flex;
  align-items: center;
`

export default function SideBar({ children }) {
  const { getItem } = useContext(StorageContext)

  const drawerView = getItem('DRAFT_drawer_view', 'explorer')
  const drawerIsOpen = getItem('DRAFT_drawer_is_open', true)
  return drawerIsOpen ? (
    <div css={drawerCss} className="demo-font">
      {/* <div css={titleCss}>
        <H3>{drawerView}</H3>
      </div> */}
      {children}
    </div>
  ) : null
}
