import React, { useContext } from 'react'
/** @jsx jsx */
import { css, jsx } from '@emotion/core'
import PropsDrawer from './PropsDrawer'
import EditDrawer from './EditDrawer'
import ActivityBar from './ActivityBar'
import SideBar from './SideBar'
import Explorer from './Explorer'
import Settings from './Settings/Settings'
import { SettingsContext } from './Settings/SettingsProvider'
import { StorageContext } from './StorageContext'
import Tabs from './Tabs'

const wrapperCss = css`
  display: grid;
  height: 100vh;
  max-height: 100vh;
  grid-template-rows: 48px 1fr;
`

const contentCss = css`
  height: 100%;
  display: flex;
  background: #283048;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #859398, #283048);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #859398, #283048); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

`

const barCss = css`
  display: flex;
  color: var(--color-text);
  background-color: var(--color-background-primary);
`

const titleCss = css`
  width: 459px;
  padding-left: 76px;
  line-height: 48px;
  text-transform: uppercase;
  font-weight: bold;
  box-sizing: border-box;
  transition: margin-left .1s ease-in-out;
`

const boxCss = css`
  padding: 16px 16px 16px 0;
  box-sizing: border-box;
  transition: all .1s ease-in-out;
`

const containerCss = css`
  box-sizing: border-box;
  box-shadow: 0 0 16px 0 #0009;
  border-radius: 3px;
  height: 100%;
  overflow: hidden;
`

const displayCss = css`
  flex-grow: 2;
`

export default function DemoWrapper({ propObjects, children, componentTree }) {
  const { getItem, setItem } = useContext(StorageContext)

  const drawerView = getItem('DRAFT_drawer_view', 'explorer')
  const drawerIsOpen = getItem('DRAFT_drawer_is_open', true)

  const editDrawerOpen = getItem('DRAFT_edit_drawer_open', false)
  const editItem = getItem('DRAFT_edit_item', { warnings: [] })

  const setEditItem = newEditItem => setItem('DRAFT_edit_item', newEditItem)

  const { settings } = useContext(SettingsContext)

  return (
    <div css={wrapperCss}>
      <div css={barCss}>
        {/* TITLE */}
        <div css={titleCss} style={{
          marginLeft: drawerIsOpen ? 0 : -459
        }}>{drawerView}</div>

        {/* TABS */}
        <Tabs />
      </div>
      <div css={contentCss}>


        {/* APP BAR */}
        <ActivityBar />

        {/* SIDEBAR */}
        <SideBar>
          {/* PROPS VIEW */}
          {drawerView === 'props' && (
            <PropsDrawer
              open={drawerIsOpen && drawerView === 'props'}
              propObjects={propObjects}
              setEditItem={setEditItem}
              openEditDrawer={() => setItem('DRAFT_edit_drawer_open', true)}
            />
          )}

          {/* EXPLORER VIEW */}
          {drawerView === 'explorer' && <Explorer componentTree={componentTree} />}

          {/* SETTINGS VIEW */}
          {drawerView === 'settings' && <Settings />}
        </SideBar>

        {/* DEMO */}
        <div css={displayCss}>
          <div css={boxCss} style={{paddingLeft: drawerIsOpen ? 16 : 0}}>
            <div
              css={containerCss}
              style={{
                padding: `${settings.demoPadding}px`,
                backgroundColor: settings.backgroundColor || '#fff',
              }}
            >
              {children}
            </div>
          </div>
        </div>

        {editDrawerOpen && (
          <EditDrawer
            open={editDrawerOpen}
            setOpen={newValue => setItem('DRAFT_edit_drawer_open', newValue)}
            editItem={editItem}
            setEditItem={setEditItem}
          />
        )}
      </div>
    </div>
  )
}
